export default {
	data() {
		return {
			tabs: 0,
			tabsData: ['科一', '科四'],
			adData: [],
			askData: [],
			newsData: []
		}
	},
	onLoad: function() {
		this.adRequest('one')
		//社区列表
		uni.request({
			url: this.$Url + '/api/school/community', //仅为示例，并非真实接口地址。
			method: 'GET',
			data: {
				// token: uni.getStorageSync('token')
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: (res) => {
				if (res.data.code == 200) {
					console.log(res.data)
					var arr = []
					for (let i in res.data.msg) {
						arr.push(res.data.msg[i]); //属性
					}
					this.newsData = arr
				} else {
					uni.showToast({
						icon: 'none',
						title: '网络不给力，请稍后重试',
						duration: 1000
					});
				}
			}
		});
	},
	methods: {
		adRequest: function(e) {
			//广告
			uni.request({
				url: this.$Url + '/api/car/ad',
				method: 'GET',
				data: {
					position: e
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					if (res.data.code == 200) {
						console.log(res.data)
						let arr = []
						for (let i in res.data.msg) {
							res.data.msg[i].image = this.$Url + res.data.msg[i].image
							arr.push(res.data.msg[i]);
						}
						this.adData = arr
					} else {
						uni.showToast({
							icon: 'none',
							title: '网络不给力，请稍后重试',
							duration: 2000
						});
					}
				}
			});
			//社区列表
		},
		tapTabs: function(e) {
			this.tabs = e.currentTarget.dataset.idx
			if (e.currentTarget.dataset.idx == 0) {
				this.adRequest('one')
			} else {
				this.adRequest('two')
			}
		},
		openBrowser: function(e) {
			plus.runtime.openURL(e.currentTarget.dataset.url)
		},
		RandomPractice: function(e) {
			if (uni.getStorageSync('logon_status') == 1) {
				uni.navigateTo({
					url: '../../examination/Random-practice/Random-practice?subject=' + e.currentTarget.dataset.idx
				});
			} else {
				uni.navigateTo({
					url: '../../login/login?form=2'
				});
			}
		},
		MockExam: function(e) {
			if (uni.getStorageSync('logon_status') == 1) {
				uni.navigateTo({
					url: '../../examination/Mock-exam/Mock-exam?subject=' + e.currentTarget.dataset.idx
				});
			} else {
				uni.navigateTo({
					url: '../../login/login?form=2'
				});
			}
		}
	}
}
